<template>
  <div>
    <div v-if="readonly">
      <el-table v-if="targetTable.length" :data="targetTable" style="width: 100%" :show-header="false">
        <el-table-column prop="name" label="" width="110"></el-table-column>
        <el-table-column prop="value" label=""> </el-table-column>
      </el-table>
    </div>
    <div v-else>
      <el-form :model="form">
        <input type="hidden" v-model="form.targeting_id">
        <div class="temp-item-block" v-if='!locked'>
          <el-form-item label="定向包名称">
            <el-col :span="6">
              <el-input v-model="form.targeting_name" :maxlength="120" placeholder="不超过120个字符"></el-input>
            </el-col>
            <el-col :span="14">
              <el-input v-model="form.description" :maxlength="250" placeholder="定向包描述，不超过250个字符" autosize></el-input>
            </el-col>
          </el-form-item>
        </div>

        <!-- 人口属性 -->
        <div class="temp-item-block">
          <div class="temp-item-title">人口属性</div>

          <div class="temp-item-main">
            <el-switch v-model="status.geo_location" inactive-text="地域" class="temp-item-main-title"> </el-switch>
            <div v-show='status.geo_location' class="temp-item-main-body">
              <v-select v-if="regions.length" :list="regions" :value="myregions" v-on:change="updateRegions"></v-select>
              <el-checkbox-group v-model="form.targeting.geo_location.location_types" style="margin-top:10px" @change="updateLocation">
                <el-checkbox v-for="(value, key) in options.location_types" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.age" inactive-text="年龄" class="temp-item-main-title"> </el-switch>
            <div v-show='status.age' class="temp-item-main-body">
              <el-input-number v-model="min_age" :min="5" :max="max_age" label="最小年龄" size="small"></el-input-number>
              ~
              <el-input-number v-model="max_age" :min="min_age" :max="66" label="最大年龄" size="small"></el-input-number>
              <span class="temp-item-remark">66 代表的是 66 岁及 66 岁以上</span>
              <p class="temp-item-main-remark">由于国家政策，若投放酒类广告，系统只会向18岁以上受众展示</p>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.gender" inactive-text="性别" class="temp-item-main-title"> </el-switch>
            <div v-show='status.gender' class="temp-item-main-body">
              <el-radio-group v-model="form.targeting.gender">
                <el-radio v-for="(value, key) in options.gender" :label="key" :key="key">{{value}}</el-radio>
              </el-radio-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.education" inactive-text="学历" class="temp-item-main-title"> </el-switch>
            <div v-show='status.education' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.education">
                <el-checkbox v-for="(value, key) in options.education" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.relationship_status" inactive-text="婚恋状态" class="temp-item-main-title"> </el-switch>
            <div v-show='status.relationship_status' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.relationship_status">
                <el-checkbox v-for="(value, key) in options.relationship_status" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.living_status" inactive-text="工作状态" class="temp-item-main-title"> </el-switch>
            <div v-show='status.living_status' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.living_status">
                <el-checkbox v-for="(value, key) in options.living_status" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
        </div>

        <!-- 用户行为 -->
        <div class="temp-item-block">
          <div class="temp-item-title">用户行为</div>
          <div class="temp-item-main">
            <el-switch v-model="status.business_interest" inactive-text="商业兴趣" class="temp-item-main-title"> </el-switch>

            <div v-show='status.business_interest' class="temp-item-main-body">
              <v-select v-if="business_interest.length" :list="business_interest" :value="form.targeting.business_interest" v-on:change="updateBusinessInterest"></v-select>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.keyword" inactive-text="关键词" title='对指定关键词感兴趣的用户' class="temp-item-main-title"> </el-switch>
            <div v-show='status.keyword' class="temp-item-main-body">
              <p v-if='form.targeting.keyword.words.length' class="keyword">已选[{{form.targeting.keyword.words.length}}]:
                <span v-for="(item,index) in form.targeting.keyword.words" @click="form.targeting.keyword.words.splice(index,1)">{{item}}</span>
                <a @click="form.targeting.keyword.words=[]" style="color:#409EFF">清空</a>
              </p>
              <el-col :span="8">
                <el-input v-model.trim="keyword" :maxlength="30" placeholder="不超过30个字符" @keyup.enter.native="keyword.length>0&&form.targeting.keyword.words.push(keyword);keyword=''" size='mini' @click.native="$event.target.select()"></el-input>
              </el-col>
              <el-col :span="8" style="margin-left:5px;">
                <el-button type="primary" @click="keyword.length>0&&form.targeting.keyword.words.push(keyword);keyword=''" icon="el-icon-circle-plus-outline" size='mini'>添加</el-button>
              </el-col>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.app_behavior" inactive-text="应用" title='特定类型App的活跃或付费用户' class="temp-item-main-title"> </el-switch>
            <div v-show='status.app_behavior'>
              <!-- <el-switch v-model="object_type" active-text="按分类" inactive-text="按App"> </el-switch> -->
              <div v-if='object_type' class="temp-item-main-body">
                <v-select v-if="app_category.length" :list="app_category" :value="form.targeting.app_behavior.object_id_list" v-on:change="updateAppCategory" :fmenu='false'></v-select>

              </div>
              <div v-else>占不支持</div>
              距离今天
              <el-input-number v-model="form.targeting.app_behavior.time_window" :min="1" :max="365" size='mini'></el-input-number> 天内， 该类APP的
              <el-checkbox-group v-model="form.targeting.app_behavior.act_id_list" style='display:inline'>
                <el-checkbox v-for="(value, key) in options.act_id_list" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>

            </div>
          </div>
          <div class="temp-item-main">
            <el-switch v-model="status.app_install_status" inactive-text="App安装" title='已安装或未安装所推广App的用户' class="temp-item-main-title">
            </el-switch>
            <div v-show='status.app_install_status' class="temp-item-main-body">
              <el-radio-group v-model="form.targeting.app_install_status">
                <el-radio v-for="(value, key) in options.app_install_status" :label="key" :key="key">{{value}}</el-radio>
              </el-radio-group>
            </div>
          </div>

        </div>
        <!-- 自定义人群 -->
        <div class="temp-item-block">
          <div class="temp-item-title">自定义人群</div>
          <div class="temp-item-main">
            <el-switch v-model="status.custom_audience" inactive-text="定向人群" title="您的广告将仅投放到选定的人或目标场景中" class="temp-item-main-title">
            </el-switch>

            <div v-show='status.custom_audience' class="temp-item-main-body">
              <p v-if='customAudience.length'>已选:
                <ul class="keyword">
                  <li v-for="(item,index) in customAudience" @click="customAudience.splice(index, 1)">{{item.label}}</li>
                </ul>
                <a @click="clearAudience()" style="color:#409EFF">清空</a>
              </p>
              <el-row style="margin:5px 0">
                <el-col :span="1">
                  <i class="el-icon-search" style="font-size: 1.5rem;line-height:2rem"></i>
                </el-col>
                <el-col :span="10">
                  <el-input v-model.trim="audienceKeyword" placeholder="请输入内容" @keyup.native="filterAudience(audienceKeyword)" class="input-with-select icon-search" size="small"></el-input>
                </el-col>
              </el-row>
              <el-table ref="audienceTable" :data="audienceList" size="mini" style="width: 100%" max-height="200" @selection-change="handleAudienceChange">
                <el-table-column type="selection" width="55"> </el-table-column>
                <el-table-column prop="label" label="用户群名称" width="250" show-overflow-tooltip> </el-table-column>
                <el-table-column prop="id" label="ID" width="120"> </el-table-column>
                <el-table-column prop="user_count" label="覆盖数量"> </el-table-column>
                <el-table-column prop="show" :filter-method="filterTag"> </el-table-column>
              </el-table>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.excluded_custom_audience" inactive-text="排除用户群" title="您的广告将不会投放到选定的人或目标场景中" class="temp-item-main-title">
            </el-switch>
            <div v-show='status.excluded_custom_audience' class="temp-item-main-body">
              <p v-if='excludedCustomAudience.length'>已选:
                <ul class="keyword">
                  <li v-for="(item,index) in excludedCustomAudience" @click="excludedCustomAudience.splice(index, 1)">{{item.label}}</li>
                </ul>
                <a @click="clearExcludedAudience()" style="color:#409EFF">清空</a>
              </p>
              <el-row style="margin:5px 0">
                <el-col :span="1">
                  <i class="el-icon-search" style="font-size: 1.5rem;line-height:2rem"></i>
                </el-col>
                <el-col :span="10">
                  <el-input placeholder="请输入内容" v-model="excludedAudienceKeyword" @keyup.native="filterExincludeAudience(excludedAudienceKeyword)" class="input-with-select icon-search" size="small"></el-input>
                </el-col>
              </el-row>
              <el-table ref="excludeAudienceTable" :data="excludeAudienceList" size="mini" style="width: 100%" max-height="200" @selection-change="handleExcludedAudienceChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="label" label="用户群名称" width="250" show-overflow-tooltip> </el-table-column>
                <el-table-column prop="id" label="ID" width="120"> </el-table-column>
                <el-table-column prop="user_count" label="覆盖数量"> </el-table-column>
                <el-table-column prop="show" :filter-method="filterTag"> </el-table-column>
              </el-table>
            </div>
          </div>

        </div>

        <!-- 消费能力 -->
        <div class="temp-item-block">
          <div class="temp-item-title">消费能力</div>
          <div class="temp-item-main">
            <el-switch v-model="status.shopping_capability" inactive-text="消费能力" class="temp-item-main-title"> </el-switch>
            <div v-show='status.shopping_capability' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.shopping_capability">
                <el-checkbox v-for="(value, key) in options.shopping_capability" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.paying_user_type" inactive-text="付费用户" class="temp-item-main-title"> </el-switch>
            <div v-show='status.paying_user_type' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.paying_user_type">
                <el-checkbox v-for="(value, key) in options.paying_user_type" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.residential_community_price" inactive-text="居住社区价格（元/m²）" class="temp-item-main-title"> </el-switch>
            <div v-show='status.residential_community_price' class="temp-item-main-body">
              <el-input-number v-model="min_community_price" :min="1000" :max="max_community_price" label="最小价格" size="small"></el-input-number>
              ~
              <el-input-number v-model="max_community_price" :min="min_community_price" :max="100000" label="最大价格" size="small"></el-input-number> 元/m²
            </div>
          </div>

        </div>

        <!-- 设备定向 -->
        <div class="temp-item-block">
          <div class="temp-item-title">设备定向</div>

          <div class="temp-item-main">
            <el-switch v-model="status.user_os" inactive-text="操作系统" class="temp-item-main-title"> </el-switch>
            <div v-show='status.user_os' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.user_os">
                <el-checkbox v-for="(value, key) in options.user_os" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.new_device" inactive-text="新设备" class="temp-item-main-title"> </el-switch>
            <div v-show='status.new_device' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.new_device">
                <el-checkbox v-for="(value, key) in options.new_device" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.network_type" inactive-text="联网方式" class="temp-item-main-title"> </el-switch>
            <div v-show='status.network_type' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.network_type">
                <el-checkbox v-for="(value, key) in options.network_type" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.network_operator" inactive-text="移动运营商" class="temp-item-main-title"> </el-switch>
            <div v-show='status.network_operator' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.network_operator">
                <el-checkbox v-for="(value, key) in options.network_operator" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
          <div class="temp-item-main">
            <el-switch v-model="status.device_price" inactive-text="设备价格" class="temp-item-main-title"> </el-switch>
            <div v-show='status.device_price' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.device_price">
                <el-checkbox v-for="(value, key) in options.device_price" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
        </div>

        <!-- 天气定向 -->
        <div class="temp-item-block">
          <div class="temp-item-title">天气定向</div>
          <div class="temp-item-main">
            <el-switch v-model="status.temperature" inactive-text="温度" class="temp-item-main-title"> </el-switch>
            <div v-show='status.temperature' class="temp-item-main-body">
              <el-input-number v-model="min_temperature" :min="-50" :max="max_temperature" label="最小温度" size="small"></el-input-number>
              ~
              <el-input-number v-model="max_temperature" :min="min_temperature" :max="50" label="最大温度" size="small"></el-input-number>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.uv_index" inactive-text="紫外线指数" class="temp-item-main-title"> </el-switch>
            <div v-show='status.uv_index' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.uv_index">
                <el-checkbox v-for="(value, key) in options.uv_index" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.dressing_index" inactive-text="穿衣指数" title="根据气温、湿度、风等气象条件，对人们适宜穿着的服装进行分级" class="temp-item-main-title">
            </el-switch>
            <div v-show='status.dressing_index' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.dressing_index">
                <el-checkbox v-for="(value, key) in options.dressing_index" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.makeup_index" inactive-text="化妆指数" title="根据气温、湿度、风等气象条件，对人们适宜穿着的服装进行分级" class="temp-item-main-title">
            </el-switch>
            <div v-show='status.makeup_index' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.makeup_index">
                <el-checkbox v-for="(value, key) in options.makeup_index" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>

          <div class="temp-item-main">
            <el-switch v-model="status.climate" inactive-text="气象" class="temp-item-main-title"> </el-switch>
            <div v-show='status.climate' class="temp-item-main-body">
              <el-checkbox-group v-model="form.targeting.climate">
                <el-checkbox v-for="(value, key) in options.climate" :label="key" :key="key">{{value}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
        </div>
        <div class="temp-item-block">
          <el-form-item v-if='locked'>
            <el-col :span="8">
              <el-checkbox v-model="saveas" @change="action_title=saveas?'创建':'保存'">定向包名称</el-checkbox>
            </el-col>
            <el-col :span="16">
              <el-input v-model="form.targeting_name" :maxlength="120" placeholder="不超过120个字符"></el-input>
            </el-col>
          </el-form-item>
        </div>
        <el-form-item>
          <el-button type="primary" @click="onSubmit" v-text='action_title'></el-button>
          <el-button @click="onCancel">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>

</template>

<script>
import options from '../util/options.js'
import vSelect from './vSelect'

export default {
  components: {
    'v-select': vSelect
  },
  props: {
    target: {
      type: Object,
      default () {
        return {}
      }
    },
    readonly: {
      type: Boolean,
      default () {
        return false
      }
    },
    locked: {
      type: Boolean,
      default () {
        return false
      }
    },
  },

  data () {
    return {
      saveas: false,
      targetTable: [],

      action_title: '创建',
      status: {
        age: false,
        gender: false,
        education: false,
        relationship_status: false,
        living_status: false,
        business_interest: false,
        keyword: false,
        geo_location: false,
        user_os: false,
        new_device: false,
        device_price: false,
        network_type: false,
        network_operator: false,
        dressing_index: false,
        uv_index: false,
        makeup_index: false,
        climate: false,
        temperature: false,
        app_install_status: false,
        app_behavior: false,
        shopping_capability: false,
        shopping_capability: false,
        paying_user_type: false,
        residential_community_price: false,
        audience: false,
        custom_audience: false,
        excluded_custom_audience: false,
        media_industry: false,
        wechatflowclass: false,
      },

      options: options,


      min_age: 5,
      max_age: 66,
      min_temperature: -50,
      max_temperature: 50,
      keyword: '',
      audienceKeyword: '',
      excludedAudienceKeyword: '',

      excludedAudienceFilter: '',
      customAudience: [],
      excludedCustomAudience: [],
      audience: [],
      audienceList: [],
      excludeAudienceList: [],


      min_community_price: 1000,
      max_community_price: 100000,

      regions: [],
      business_interest: [],
      app_category: [],
      app_behavior: '',
      object_type: true,
      form: {},
      form0: {
        targeting_id: 0,
        targeting_name: '',
        description: '',
        targeting: {
          gender: [],
          age: [],
          education: [],
          relationship_status: [],
          living_status: [],
          business_interest: [],
          app_category: [],
          keyword: {
            words: []
          },

          geo_location: {
            location_types: [],
            regions: [],
            business_districts: [],
          },
          user_os: [],
          new_device: [],
          device_price: [],
          network_type: [],
          network_operator: [],
          dressing_index: [], //穿衣指数
          uv_index: [], //紫外线指数

          makeup_index: [],
          climate: [],
          temperature: [],
          app_install_status: [],
          app_behavior: {
            object_type: 'APP_CLASS',
            time_window: 1,
            act_id_list: [],
            object_id_list: [],
          },
          shopping_capability: [],
          player_consupt: [],
          paying_user_type: [],
          residential_community_price: [],
          custom_audience: [],
          excluded_custom_audience: [],
        }
      },

      rules: {
        targeting_name: [{
          required: true,
          message: '请输入定向包名称',
          trigger: 'blur'
        },
        {
          min: 1,
          max: 15,
          message: '长度在 1 到 5 个字符',
          trigger: 'blur'
        }
        ]
      }
    }
  },
  created: function () {
    this.targetApi()
    this.bingParams()
  },
  computed: {
    audienceIds () {
      var ret = []
      this.customAudience.forEach(element => {
        ret.push(element.id)
      });
      return ret
    },
    excludeAudienceIds () {
      var ret = []
      this.excludedCustomAudience.forEach(element => {
        ret.push(element.id)
      });
      return ret
    },
    account_id () {
      var account = this.$store.getters.get_account_action
      return account.account_id
    },
    myregions () {
      var val = []
      if (this.form.targeting.geo_location.hasOwnProperty('regions') && this.form.targeting.geo_location.regions.length) {
        val = val.concat(this.form.targeting.geo_location.regions)
      }
      if (this.form.targeting.geo_location.hasOwnProperty('business_districts') && this.form.targeting.geo_location
        .business_districts
        .length) {
        val = val.concat(this.form.targeting.geo_location.business_districts)
      }
      return val
    },
    appCategoryMap () {
      var map = {}
      this.app_category.forEach(element => {
        map[element.id] = element.label
      });
      return map
    },
    businessInterestMap () {
      var map = {}
      this.business_interest.forEach(element => {
        map[element.id] = element.label
      });
      return map
    },

    regionsMap () {
      var map = {}
      this.regions.forEach(element => {
        map[element.id] = element.label
      });
      return map
    },

    audienceMap () {
      var map = {}
      this.audienceList.forEach(element => {
        map[element.id] = element.label
      });
      return map
    },


    age () {
      return [this.min_age + '~' + this.max_age]
    },
    residential_community_price () {
      return [this.min_community_price + '~' + this.max_community_price]
    },
    temperature () {
      return [(this.min_temperature + 223) + '~' + (this.max_temperature + 223)]
    }
  },
  watch: {
    account_id () {
      this.targetApi()
    },
    target (newval, oldval) {
      this.bingParams()
    },

    age (newval, oldval) {
      this.form.targeting.age = newval
    },
    residential_community_price (newval, oldval) {
      this.form.targeting.residential_community_price = newval
    },
    temperature (newval, oldval) {
      this.form.targeting.temperature = newval
    },

  },


  mounted () { },
  methods: {
    getTest () {
      console.log(this.form.targeting.geo_location);
    },
    cloneObj (obj) {
      var str, newobj = obj.constructor === Array ? [] : {};
      if (typeof obj !== 'object') {
        return;
      } else if (window.JSON) {
        newobj = JSON.parse(JSON.stringify(obj));
      } else {
        for (var i in obj) {
          newobj[i] = typeof obj[i] === 'object' ? cloneObj(obj[i]) : obj[i];
        }
      }
      return newobj;
    },




    bingParams () {
      if (this.readonly) {
        var targetTable = []
        var mytargettable = {}
        var ret = []

        for (const key in this.target.targeting) {
          var val = this.target.targeting[key]
          mytargettable = {
            'type': key,
          }
          if (this.options.targeting_types.hasOwnProperty(key)) {
            mytargettable['name'] = this.options.targeting_types[key]
          }
          switch (key) {
            case 'geo_location':
              ret = []
              if (val.hasOwnProperty('location_types')) {
                ret.push("(" + this.getTargetVal('location_types', val['location_types']).join() + ")")
              }
              if (val.hasOwnProperty('regions')) {
                ret.push(this.getRegionsVal(val['regions']).join())
              }
              mytargettable['value'] = ret.join()
              break;
            case 'app_behavior':
              mytargettable['value'] = '距离今天 ' + val['time_window'] + ' 天内，[' + this.getAppCategoryVal(val[
                'object_id_list']).join() + '] 类APP的 ' + this.getTargetVal('act_id_list', val['act_id_list']).join()
              break;
            case 'business_interest':
              mytargettable['value'] = this.getBusinessInterestVal(val).join()
              break;
            case 'custom_audience':
              mytargettable['value'] = this.getCustomAudienceVal(val).join(',')
              break;
            case 'excluded_custom_audience':
              mytargettable['value'] = this.getExcludeCustomAudienceVal(val).join(',')
              break;
            case 'age':
            case 'residential_community_price':
            case 'temperature':
              mytargettable['value'] = val[0]
              break;
            case 'app_install_status':
              mytargettable['value'] = this.getTargetVal(key, [val]).join()
              break;
            case 'keyword':
              mytargettable['value'] = val['words'].join()
              break;
            default:
              mytargettable['value'] = this.getTargetVal(key, val).join()
              break;
          }
          targetTable.push(mytargettable)
        }

        this.targetTable = targetTable


      } else {
        if (this.target.targeting_id) {
          this.action_title = '保存'
        }
        this.form = this.cloneObj(this.form0)

        if (!this.locked) {
          this.form.targeting_id = this.target.targeting_id
          this.form.targeting_name = this.target.targeting_name
          this.form.description = this.target.description
        }
        for (const key in this.status) {
          this.status[key] = false
        }
        for (const key in this.target.targeting) {
          if (this.status.hasOwnProperty(key)) {
            this.status[key] = true
          }
          if (this.form.targeting.hasOwnProperty(key)) {
            this.form.targeting[key] = this.target.targeting[key]
            switch (key) {
              case 'age':
                this.splitAge(this.form.targeting[key][0])
                break;
              case 'residential_community_price':
                this.splitCommunityPrice(this.form.targeting[key][0])
                break;
              case 'temperature':
                this.splitTemperature(this.form.targeting[key][0])
                break;
              case 'app_behavior':
                if (this.form.targeting[key].hasOwnProperty('object_id_list')) {
                  var ret = []
                  this.form.targeting[key].object_id_list.forEach(element => {
                    ret.push(parseInt(element))
                  });
                  this.form.targeting[key].object_id_list = ret
                }
                break;
              case 'custom_audience':
                this.bindAudienceSelect(this.form.targeting[key])
                break;
              case 'excluded_custom_audience':
                this.bindExcludedAudienceSelect(this.form.targeting[key])
                break;
              default:
                break;
            }
          }
        }
      }

    },

    targetApi () {
      if (!this.account_id) {
        return
      }
      var params = {
        account_id: this.account_id
      }

      this.regions = this.options.region
      this.business_interest = this.options.business_interest
      this.app_category = this.options.app_category

      var that = this
      // this.$axios
      //   .get('/Admin/Marketing_Targetings/getTree?type=REGION', {
      //     params: params
      //   }).then(function (response) {
      //     var respData = response.data
      //     if (respData.code) {
      //       that.$message(respData.msg)
      //     } else {
      //       that.regions = respData.data
      //     }
      //   })
      //   .catch(function (err) {
      //     that.$message('网络繁忙，请稍后再试！')
      //     console.log(err)
      //   })

      // this.$axios
      //   .get('/Admin/Marketing_Targetings/getTree?type=BUSINESS_INTEREST', {
      //     params: params
      //   }).then(function (response) {
      //     var respData = response.data
      //     if (respData.code) {
      //       that.$message(respData.msg)
      //     } else {
      //       that.business_interest = respData.data
      //     }
      //   })
      //   .catch(function (err) {
      //     that.$message('网络繁忙，请稍后再试！')
      //     console.log(err)
      //   })

      // this.$axios
      //   .get('/Admin/Marketing_Targetings/getTree?type=APP_CATEGORY', {
      //     params: params
      //   }).then(function (response) {
      //     var respData = response.data
      //     if (respData.code) {
      //       that.$message(respData.msg)
      //     } else {
      //       that.app_category = respData.data
      //     }
      //   })
      //   .catch(function (err) {
      //     that.$message('网络繁忙，请稍后再试！')
      //     console.log(err)
      //   })

      this.$axios
        .get('/Admin/Marketing_CustomAudiences/get', {
          params: params
        }).then(function (response) {
          var respData = response.data
          if (respData.code) {
            that.$message(respData.msg)
          } else {
            that.audience = respData.data
            that.audienceList = respData.data
            that.excludeAudienceList = respData.data
            that.$nextTick(function () {
              that.bingParams()
            })
          }
        })
        .catch(function (err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },

    getBusinessInterestVal (val) {
      var ret = []
      val.forEach(element => {
        if (this.businessInterestMap.hasOwnProperty(element)) {
          ret.push(this.businessInterestMap[element])
        }
      })
      return ret
    },
    getCustomAudienceVal (val) {
      var ret = []
      var map = {}
      var count = 0
      this.audienceList.forEach(element => {
        map[element.id] = element
      })

      val.forEach(id => {
        if (map.hasOwnProperty(id)) {
          ret.push(map[id]['label'])
        }
      })
      return ret
    },
    getExcludeCustomAudienceVal (val) {
      var ret = []
      var map = {}
      var count = 0
      this.excludeAudienceList.forEach(element => {
        map[element.id] = element
      })
      val.forEach(id => {
        if (map.hasOwnProperty(id)) {
          ret.push(map[id]['label'])
        }
      })
      return ret
    },
    getRegionsVal (val) {
      var ret = []
      val.forEach(element => {
        if (this.regionsMap.hasOwnProperty(element)) {
          ret.push(this.regionsMap[element])
        }
      })
      return ret
    },

    getAppCategoryVal (val) {
      var ret = []
      val.forEach(element => {
        if (this.appCategoryMap.hasOwnProperty(element)) {
          ret.push(this.appCategoryMap[element])
        }
      })
      return ret
    },
    getTargetVal (key, val) {
      var ret = []
      if (this.options.hasOwnProperty(key)) {
        if (val instanceof Array) {
          val.forEach(element => {
            if (this.options[key].hasOwnProperty(element)) {
              ret.push(this.options[key][element])
            }
          })
        } else {
          if (this.options[key].hasOwnProperty(val)) {
            ret.push(this.options[key][val])
          }
        }
      }
      return ret
    },

    updateLocation () {
      // if (this.form.targeting.geo_location.business_districts.length) {
      //   this.form.targeting.geo_location.location_types = ['VISITED_IN']
      // }
    },

    updateRegions (vals) {
      var areas = []
      var zones = []
      vals.forEach(element => {
        if (element.id > 1000000) {
          zones.push(element.id)
        } else {
          areas.push(parseInt(element.id))
        }

      });
      // if (zones.length) {
      //   this.form.targeting.geo_location.location_types = ['TRAVEL_IN']
      // }
      this.form.targeting.geo_location.regions = areas
      this.form.targeting.geo_location.business_districts = zones
    },

    updateBusinessInterest (vals) {
      var ret = []
      vals.forEach(element => {
        ret.push(element.id)
      });

      this.form.targeting.business_interest = ret
    },

    updateAppCategory (vals) {
      var ret = []
      vals.forEach(element => {
        ret.push(element.id)
      });
      this.form.targeting.app_behavior.object_id_list = ret
    },


    splitAge (range) {
      var age = range.split('~')
      this.min_age = parseInt(age[0])
      this.max_age = parseInt(age[1])
    },
    splitCommunityPrice (range) {
      var price = range.split('~')
      this.min_community_price = parseInt(price[0])
      this.max_community_price = parseInt(price[1])
    },
    splitTemperature (range) {
      var temperature = range.split('~')
      this.min_temperature = parseInt(temperature[0]) - 223
      this.max_temperature = parseInt(temperature[1]) - 223
    },

    bindAudienceSelect (AudienceIds) {
      if (AudienceIds.length) {
        this.audienceList.forEach(element => {
          if (AudienceIds.indexOf(element.id) >= 0) {
            this.$refs.audienceTable.toggleRowSelection(element, true);
          }
        });
      }

    },
    bindExcludedAudienceSelect (ExcludedAudienceIds) {
      if (ExcludedAudienceIds.length) {
        this.excludeAudienceList.forEach(element => {
          if (ExcludedAudienceIds.indexOf(element.id) >= 0) {
            this.$refs.excludeAudienceTable.toggleRowSelection(element, true);
          }
        });
      }
    },

    filterTag (value, row) {
      return row.show === value;
    },

    handleAudienceChange (items) {
      this.customAudience = items
      this.form.targeting.custom_audience = this.audienceIds
    },

    handleExcludedAudienceChange (items) {
      this.excludedCustomAudience = items
      this.form.targeting.excluded_custom_audience = this.excludeAudienceIds
    },

    clearAudience () {
      this.customAudience = []
      this.$refs.audienceTable.clearSelection();
    },

    clearExcludedAudience () {
      this.excludedCustomAudience = []
      this.$refs.excludeAudienceTable.clearSelection();
    },


    filterAudience (val) {
      this.audienceList.forEach((element, index) => {
        if (val == '') {
          this.audienceList[index]['show'] = true
          this.$refs.audienceTable.clearFilter()
        } else {
          this.audienceList[index]['show'] = element.label.indexOf(val) > -1 ? true : false
        }
      });
      this.$refs.audienceTable.store.states.columns.forEach(column => {
        if (column.hasOwnProperty('filterMethod')) {
          this.$refs.audienceTable.store.commit('filterChange', {
            values: true,
            column
          })
        }
      });
    },

    filterExincludeAudience (val) {
      this.excludeAudienceList.forEach((element, index) => {
        if (val == '') {
          this.excludeAudienceList[index]['show'] = true
          this.$refs.excludeAudienceTable.clearFilter()
        } else {
          this.excludeAudienceList[index]['show'] = element.label.indexOf(val) > -1 ? true : false
        }
      });
      this.$refs.excludeAudienceTable.store.states.columns.forEach(column => {
        if (column.hasOwnProperty('filterMethod')) {
          this.$refs.excludeAudienceTable.store.commit('filterChange', {
            values: true,
            column
          })
        }
      });
    },



    onCancel () {
      this.$emit('change')
    },
    onSubmit () {

      // this.$refs['tagetForm'].validate((valid) => {
      //   if (valid) {
      //     alert('submit!');
      //   } else {
      //     console.log('error submit!!');
      //     return false;
      //   }
      // });
      // console.log(this.form.targeting);
      // return;

      var params = {
        targeting_id: 0,
        targeting_name: '',
        description: '',
        targeting: {}
      }





      for (const key in this.form) {
        if (key == 'targeting') {
          for (const skey in this.form.targeting) {
            if (this.status[skey]) {
              params.targeting[skey] = this.form.targeting[skey]
            }
          }
        } else {
          params[key] = this.form[key]
        }
      }
      // console.log('params--', params);



      // this.form.targeting.forEach((element, index) => {
      //   console.log(index);
      //   if (this[index]) {
      //     params[index] = element
      //   }
      // });

      if (this.locked == this.saveas) {
        var that = this
        if (params.targeting_name.length == 0) {
          that.$message('定向名称不能为空')
          return
        }

        if (params.targeting.hasOwnProperty('geo_location')) {
          var mylocation = params.targeting.geo_location
          if (mylocation.hasOwnProperty('business_districts') && mylocation.business_districts.length > 2) {
            that.$message('地点类型最多支持2ge')
          } else if (mylocation.hasOwnProperty('business_districts') && mylocation.business_districts.length > 0 && !
            this.validLocation(
              mylocation.location_types, [
                'VISITED_IN'
              ])) {
            that.$message('地点类型仅支持:[' + this.options.location_types['VISITED_IN'] + ']')
            return
          } else if (mylocation.hasOwnProperty('regions') && mylocation.regions.length > 0 && !this.validLocation(
            mylocation.location_types, ['VISITED_IN'])) {
            that.$message('地点类型仅支持:[' + this.options.location_types[
              'VISITED_IN'] + ']')
            return
          }
        }


        params['account_id'] = this.account_id
        this.$axios
          .post('/Admin/Marketing_Targetings/' + ((params.targeting_id > 0 && !that.locked) ? 'update' : 'add'),
          params)
          .then(function (response) {
            var respData = response.data
            if (respData.code == 0) {
              that.saveas = false
              params.targeting_id = respData.data.targeting_id
              that.$emit('change', params)
            } else {
              that.$message.error(respData.msg)
            }
          })
          .catch(function (err) {
            that.$message('网络繁忙，请稍后再试！')
            console.log(err)
          })
      } else {
        params.targeting_id = 0
        params.targeting_name = '无定向ID'
        this.$emit('change', params)
      }
    },
    validLocation (val, items) {
      var ret = false
      val.forEach(element => {
        if (!items.includes(element)) {
          return false
        }
        ret = true
      });
      return ret
    }


  }
}

</script>


<style scoped>
/* .temp-item-main-title {
      padding-bottom: 10px;
    } */

.temp-item-main-body {
  margin: 1em 2em;
}

.temp-item-main-remark {
  display: block;
  line-height: 1.5em;
  margin-top: 0.5em;
  font-size: 14px;
  color: #999;
}

.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}

.temp-radio-remark {
  display: block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}

.temp-item-main {
  padding: 5px;
  border-bottom: 1px solid #eee;
}

.temp-item-main-title {
  font-size: 14px;
  color: #555;
  display: block;
}

.temp-item-main:hover {
  background-color: #f6f6f6;
}

.el-switch {
  display: block;
  cursor: pointer;
  padding: 10px;
}

.el-switch span.el-switch__core {
  position: absolute;
  right: 0;
}

.el-tooltip__popper.is-light {
  border: 0;
}

.keyword {
  user-select: none;
}

.keyword span {
  line-height: 1.8rem;
  display: inline-block;
  margin: 0 5px 5px 0;
  padding: 0 5px 0 15px;
  border: 1px solid #999;
  border-radius: 2rem;
  background-color: #f1f1f1;
  text-align: center;
  cursor: pointer;
  transition: padding 0.2s;
}

.keyword span::after {
  content: '×';
  color: #409eff;
  margin-left: 3px;
  opacity: 0;
  transition: opacity 0.5s;
}

.keyword span:hover::after {
  opacity: 1;
  transition: opacity 0.2s;
}
</style>

<style>
.el-switch.temp-item-main-title .el-switch__core,
.el-switch.temp-item-main-title.is-checked .el-switch__core {
  display: block;
  float: right;
}

.temp-item-title {
  display: block;
  font-size: 18px;
  margin-bottom: 10px;
}
</style>



