<style scoped>
  .mselect {
    border: 1px solid #ccc;
    overflow: hidden;
    height: 240px;
  }

  .checked {
    user-select: none;
  }

  .checked span {
    line-height: 1.8rem;
    display: inline-block;
    margin: 0 5px 5px 0;
    padding: 0 5px 0 15px;
    border: 1px solid #999;
    border-radius: 2rem;
    background-color: #f1f1f1;
    text-align: center;
    cursor: pointer;
    transition: padding .2s;
  }

  .checked span::after {
    content: "×";
    color: #409EFF;
    margin-left: 3px;
    opacity: 0;
    transition: opacity .5s;
  }

  .checked span:hover::after {
    opacity: 1;
    transition: opacity .2s;
  }


  ul {
    background-color: #fff;
    display: block;
    float: left;
    min-width: 100px;
    height: 100%;
    overflow-y: auto;
    box-shadow: inset 0 0 1px #999;

  }

  li {
    padding: 10px 50px 10px 10px;
    font-size: 1em;
    color: #444;
    cursor: pointer;
  }

  li.focus {
    /* display:block; */
    background-color: #fee;
  }

</style>
<template>
  <div>
    <!-- v-if="myval.length" -->
    <p @click="mytest()" class='checked'>已选[{{myval.length}}]:
      <span v-for="item in myval" @click="deleteCheck(item)">{{item.label}}</span>
    </p>
    <div class='mselect'>
      <ul v-for="(items,index) in node" v-if="items.length" :style="{'width':nodewidth+'%'}">
        <li v-for="item in items" @click="handleFocus(item,index)" @dblclick="handleDblCheck(item,index)" :class="{focus:item.focus}">
          <el-checkbox :indeterminate="item.indeterminate" @change="handleChange(item,index)" v-model='item.check' v-if="index||fmenu"></el-checkbox>
          {{item.label}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    props: {

      list: {
        type: Array,
        default () {
          return []
        }
      },
      value: {
        type: Array,
        default () {
          return []
        }
      },
      fmenu: {
        type: Boolean,
        default: true
      },

    },

    data() {
      return {
        tree: {},
        node: [],
        myval: []
      }
    },
    watch: {
      value(newval, oldval) {
        var val = []
        this.myval.forEach(element => {
          val.push(element.id)
        });
        if (val.toString() != newval.toString()) {
          this.myinit()
        }
      }
    },
    created() {
      this.list.sort(function (a, b) {
        return a.id - b.id
      }).forEach(element => {
        element['check'] = false
        element['focus'] = false
        element['indeterminate'] = false
        element['last'] = false
        if (!this.tree.hasOwnProperty(element.pid)) {
          this.tree[element.pid] = []
        }
        this.tree[element.pid].push(element)
      })
      this.myinit()
    },
    computed: {
      nodewidth() {
        return parseFloat(100 / this.node.length);
      }
    },
    methods: {
      mytest() {
        // console.log(this.myval);
        // console.log(this.tree);
        // console.log(this.value);

      },
      myinit() {

        this.bindCheck(0)
        this.node[0] = this.tree[0]
        this.myval = this.mycheck(0)
        this.node[0].forEach(element => {
          if (element.check || element.indeterminate) {
            this.handleFocus(element, 0)
            return
          }
        });


      },

      handleDblCheck(item, level) {
        if (level || this.fmenu) {
          item.check = !item.check
          this.handleChange(item, level)
        }
      },

      precheck(item, level, check, indeterminate) {
        if (level >= 0) {
          this.node[level].forEach(element => {
            if (element.id == item.pid) {
              element.indeterminate = check ? false : indeterminate
              element.check = check
              item = element
            }
          });
          this.node[level].forEach(element => {
            indeterminate = indeterminate || element.indeterminate || element.check
            check = check && element.check
          });
          this.precheck(item, level - 1, check, indeterminate)
        }
      },
      subcheck(item, check) {
        if (this.tree.hasOwnProperty(item.id)) {
          this.tree[item.id].forEach(element => {
            element.check = item.check
            element.indeterminate = item.indeterminate
            this.subcheck(element, item.check)
          });
        }
      },
      handleChange(item, level) {

        var indeterminate = false;
        var check = true;
        item.indeterminate = false
        item.focus = true
        this.node[level].forEach(element => {
          indeterminate = indeterminate || element.indeterminate || element.check
          check = check && element.check
        });
        this.precheck(item, level - 1, check, indeterminate)
        this.subcheck(item, check)
        this.myval = this.mycheck(0)
        this.$emit('change', this.myval)
      },

      deleteCheck(item) {
        this.myval.forEach((element, index) => {
          if (item.id == element.id) {
            this.myval.splice(index, 1);
          }
        })
        item.check = false
        if (this.tree.hasOwnProperty(item.id)) {
          this.tree[item.id].forEach(element => {
            element.check = false
            element.indeterminate = false
            this.deleteCheck(element)
          })
        }
        this.precheck(item, item.level, item.check, item.indeterminate)
      },

      bindCheck(rootid = 0, allCheck = false) {
        var hasCheck = false
        var hasIndeterminate = false
        if (this.tree.hasOwnProperty(rootid)) {
          this.tree[rootid].forEach(element => {
            if (allCheck || (this.value.length > 0 && this.value.indexOf(element.id) >= 0)) {
              element.check = true
              hasCheck = true
              this.bindCheck(element.id, element.check)
            } else {
              allCheck = false
              var ret = this.bindCheck(element.id)
              element.check = ret[0]
              element.indeterminate = ret[1]
              hasIndeterminate = hasIndeterminate || element.check || element.indeterminate
            }
          })
        }
        return [allCheck, allCheck ? false : hasCheck || hasIndeterminate]
      },

      mycheck(id = 0, level = 0) {

        var myval = []
        this.tree[id].forEach(element => {
          if (element.check) {
            element.level = level
            myval.push(element)
          } else if (element.indeterminate) {
            this.mycheck(element.id, ++level).forEach(element => {
              myval.push(element)
            })
          }
        });
        return myval
      },

      handleFocus(item, level) {
        if (item.focus) {
          this.node.splice()
        } else {
          this.node[level].forEach(element => {
            element.focus = false
          });
          item.focus = true
          this.subfocus(item, ++level)
        }
      },

      subfocus(item, level) {
        this.node.splice(level)
        if (this.tree.hasOwnProperty(item.id)) {
          var mynode = this.tree[item.id]
          this.node.push(mynode)
          mynode.forEach(element => {
            if (!this.tree.hasOwnProperty(element.id)) {
              element.last = true
            }
            if (element.focus) {
              this.subfocus(element, ++level)
            }
          });
        }
      },
    }
  }

</script>



